.App {
  text-align: center;
}

.user-name {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 100%;
    color: #000;
    font-family: "Inter", "Arial", "sans-serif";
    font-style: normal;
}

.user-name__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    margin: 74px 0 123px 0;
}

.user-name__form {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.user-name__label {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 410px;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    color: #000000;
}

.user-name__input {
    border: none;
    text-align: right;
}

.user-name__input:active {
    border: none;
}

.user-name__error {
    width: 100%;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #EE3465;
    text-align: center;
    visibility: hidden;
    margin-bottom: 30px;
}

.user-name__error_visible {
    visibility: visible;
}

.user-name__line {
    border-bottom: 1px solid #E8E8E8;
    width: 410px;
    margin: 4px 0 17px 0;
}

.user-name__input_error {
    color: #EE3465;
}
.button {
    border: 2px solid black;
    border-radius: 10px;
    outline: none;
    padding: 0;    
    font: inherit;
    background-color: inherit;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    color: #000000;
    width: 410px;
    height: 40px;
}

.button:hover {
    cursor: pointer;
    opacity: 0.7;
}

.button_color {
    font-weight: 500;
    color: #EE3465;
}

.button__disabled {
    color: #C2C2C2;
    border: 2px solid #C2C2C2;
}
